import React from 'react'
import auctionImg from '../../../style/assets/images/auction.svg';
import vitruvianImg from '../../../style/assets/images/vitruvian.png';
import silverpepeImg from '../../../style/assets/images/silverpepe.png';

const Auction = () => (
    <section className="auctionsec">
      <div className="container">
        <div className="row">
          <div className="col-sm-12 col-md-12 mb-5 heading-line">
            <h4 className="section-title">
              <img src={auctionImg} alt='' className="head-icon" />
              VLAD NFT Facts
            </h4>
          </div>
        </div>
      </div>
      {/* Slider */}
      <div className="container">
        <div className="row">
          <div className="col-sm-12 col-md-12">
            <div className="nftfactslide">
              <ul className="rotator">
                {/* NFT Fact Block 1 */}
                <li>
                  <div className="slide-block">
                    <img
                      src={vitruvianImg}
                      alt="Vitruvian Androgyne"
                      className="img-fluid"
                    />
                    <div className="slide-head">
                      <h6 className="inner-title">Vitruvian Androgyne</h6>
                      <p>
                        By{" "}
                        <a
                          href="https://twitter.com/Sushinobi_nft"
                          target="_blank" rel="noreferrer"
                        >
                          Sushinobi
                        </a>
                      </p>
                      <div className="slide-smcontent">
                        <p>Auction 2/6</p>
                        <p>
                          Highest Bid <span className="b">100 VLAD </span>
                          <span className="aucprice">$56.49</span>
                        </p>
                      </div>
                    </div>
                  </div>
                </li>
                {/* NFT Fact Block 2 */}
                <li>
                  <div className="slide-block">
                    <img
                      src={silverpepeImg}
                      alt="Silver Pepe"
                      className="img-fluid"
                    />
                    <div className="slide-head">
                      <h6>Silver Pepe</h6>
                      <p>
                        By{" "}
                        <a href="https://twitter.com/pepe" target="_blank" rel="noreferrer">
                          Mr. Pepe
                        </a>
                      </p>
                      <div className="slide-smcontent">
                        <p>Auction 2/6</p>
                        <p>
                          Highest Bid <span className="b">69 VLAD </span>
                          <span className="aucprice">$56.49</span>
                          <img
                            src="assets/images/upgraph.svg"
                            alt="up arrow"
                            className="img-fluid"
                          />
                          <span />
                        </p>
                      </div>
                    </div>
                  </div>
                </li>
                <li>
                  <div className="slide-block">
                    <img
                      src="assets/images/rugs.png"
                      alt="Greatest Rugs"
                      className="img-fluid"
                    />
                    <div className="slide-head">
                      <h6>Greatest Rugs</h6>
                      <p>
                        By{" "}
                        <a
                          href="https://twitter.com/Sushinobi_nft"
                          target="_blank" rel="noreferrer"
                        >
                          Yanni
                        </a>
                      </p>
                      <div className="slide-smcontent">
                        <p>Auction 2/6</p>
                        <p>
                          Highest Bid <span className="b">0.05 BNB </span>
                          <span className="aucprice">$56.49</span>
                        </p>
                      </div>
                    </div>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
  

export default Auction
